@import "colors";
@import "header";
@import "form";
@import "form-filters";
@import "item";
@import "loader";
@import "results-nav";
@import "footer";

* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  padding: 0;
  margin: 0;
  background-color: $body-bg;
}

.container {
  width: 100%;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
  background-color: $container-bg;
  color: $bright-font;
  position: relative;
}

.hidden {
  display: none;
}

.main {
  min-height: calc(100vh - 170px);
}
