.footer {
  display: block;
  text-align: center;
  width: 100%;
  height: 50px;
  line-height: 50px;
  background-color: $item-bg;
  &__text {
    margin: 0;
  }
  &__link {
    text-decoration: none;
    color: inherit;
    &:hover {
      text-decoration: underline;
    }
  }
}
